body {
  letter-spacing: .02em;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

h2, h3, p {
  margin: 0;
}

h2 {
  margin-bottom: 20px;
}

img {
  max-width: 100%;
  height: auto;
}

.is-hidden {
  display: none;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.breed-select {
  width: 22%;
  margin-bottom: 20px;
  padding: 10px;
}

.loader {
  color: #ff3d00;
  letter-spacing: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading";
  color: #fff;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  animation: animloader 10s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

@keyframes animloader {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

.error {
  color: red;
  font-size: xx-large;
  display: none;
}

.cat-info {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

.cat-info-text {
  margin-bottom: 15px;
}

.cat-info-text-bold {
  font-weight: 700;
}

.cat-info-text-temp {
  font-weight: 400;
}

